//npm run deploy
import { useState, useEffect } from "react";
import "./App.css";
import { db } from "./firebase-config";
import {
  collection,
  getDocs,
  addDoc,
  updateDoc,
  deleteDoc,
  doc,
} from "firebase/firestore";
import Button from '@mui/material/Button';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { purple,red,blue,green} from '@mui/material/colors';
import TextField from '@mui/material/TextField';

import Stack from '@mui/material/Stack';

import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';


import SaveSharpIcon from '@mui/icons-material/SaveSharp';
import DeleteForeverSharpIcon from '@mui/icons-material/DeleteForeverSharp';
const theme = createTheme({
  palette: {
    blue: {
      // Purple and green play nicely together.
      main: blue[500],
    },
    red: {
      // This is green.A700 as hex.
      main: red[600],
    },
    green: {
      // This is green.A700 as hex.
      main: green[600],
    },

  },
});


const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));
function App() {
  const [newName, setNewName] = useState("");
  const [newAge, setNewAge] = useState("");

  const [users, setUsers] = useState([]);
  const usersCollectionRef = collection(db, "users");

  const createUser = async () => {
    await addDoc(usersCollectionRef, { name: newName, age: String(newAge) });
    window.location.reload(); 
  };

  const updateUser = async (id, age) => {
    const userDoc = doc(db, "users", id);
    const newFields = { age };
    await updateDoc(userDoc, newFields);
  };

  const deleteUser = async (id) => {
    const userDoc = doc(db, "users", id);
    await deleteDoc(userDoc);
    window.location.reload(); 
  };

  useEffect(() => {
    const getUsers = async () => {
      const data = await getDocs(usersCollectionRef);
      setUsers(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
    };

    getUsers();
  }, []);

  return (
    
    <div className="App">


<Box sx={{ flexGrow: 1 }}>
      <Grid container spacing={2}>
        <Grid item xs={6} md={6}>

        {users.map((user) => {
        return (
          <div>
            {" "}
            <Box
      sx={{
        borderRadius: '16px',
        marginLeft:'10%',
        width: '80%',
        
        backgroundColor: '#eceff1',
        '&:hover': {
          backgroundColor: '#cfd8dc',
          opacity: [0.9, 0.8, 0.7],
        },
      }}>
            <h1>Topic: {user.name}</h1>
            <h3>Detail: {user.age}</h3>
            <ThemeProvider theme={theme}>
<DeleteForeverSharpIcon color="red"   onClick={() => {
                deleteUser(user.id);
              }} />
</ThemeProvider>
        </Box>


          </div>
        );
      })}
        </Grid>
        <Grid item xs={6} md={6}>
          
        <Box
      sx={{
        borderRadius: '16px',
        marginTop:'30%',
        width: '85%',
        height:'cover',
        backgroundColor: '#eceff1',
        '&:hover': {
          backgroundColor: '#cfd8dc',
          opacity: [0.9, 0.8, 0.7],
        },
      }}>
        <div>
<TextField id="outlined-basic" label="Topic" variant="outlined" 
style={{ m: 1, width: '95%' , height:'95%'    ,  marginTop:'5%',}}
          onChange={(event) => {
          setNewName(event.target.value);
        }}/>

</div>
<br></br>
<div>
<TextField id="outlined-basic" label="Detail" variant="outlined"   multiline numberOfLines={4}
     style={{ m: 1, width:'95%' , height:'95%' }}
        onChange={(event) => {
          setNewAge(event.target.value);
        }}/>
</div>
<br></br>

<ThemeProvider theme={theme}>
<Button  onClick={createUser}  variant="contained" color="green" style={{marginBottom:'5%'}}>
<SaveSharpIcon/>
Save  </Button >
</ThemeProvider>
    </Box>

        </Grid>
      </Grid>
    </Box>


    </div>
  );
}

export default App;
